import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { addItems, getItems } from "../api";
import Authenticated from "../utils/Auth";
import AddBtn from "../components/AddBtn";

export default function Items() {
    const navigate = useNavigate();
    const [items, setItems] = useState([]);
    const [item, setItem] = useState("");
    const [userType, setUserType] = useState();
    useEffect(() => {
        Authenticated()
            .then((data) => {
                if (data.isAuth)
                    setUserType(data.user.type);
                else{
                    navigate('/');
                    window.alert("session expired");
                }
            });
        getItems()
            .then((data) => {
                setItems(data);
            })
    }, []);

    const handleSubmit = (e) => {
        e.preventDefault();
        addItems({item: item})
        .then((data)=>{
            window.alert('added successfull');
            console.log(data);
        }).catch((err)=>console.log(err));
    }

    return (
            <div className="container-sm">
                <div className="d-flex flex-wrap">
                {items.map((item)=>{
                    return <span className="p-2 rounded me-3 mb-3 fs-4" style={{backgroundColor: "#e3f2fd", border:"2px solid rgba(0,0,255,0.2)"}}>{item.itemName}</span>
                })}
                </div>
                {userType=="munim"?null:<AddBtn/>}
                <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                    <div class="modal-dialog modal-dialog-centered">
                        <div class="modal-content">
                            <form onSubmit={handleSubmit}>
                                <div class="modal-header">
                                    <h1 class="modal-title fs-5" id="staticBackdropLabel">Add Item</h1>
                                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                </div>
                                <div class="modal-body">

                                    <lable>Enter Item : </lable>
                                    <input className="mt-3 form-control" value={item} onChange={(e) => { setItem(e.target.value) }} placeholder="enter" />

                                </div>
                                <div class="modal-footer">
                                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                    <button type="submit" class="btn btn-primary" data-bs-dismiss="modal">Add</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
    );
}