import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { addPayment, getPaymentsById } from "../api";
import Authenticated from "../utils/Auth";
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
import { Helper } from "../utils/Helper";
import AddBtn from "../components/AddBtn";

export default function ClientPayments() {
    const params = useParams();
    const navigate = useNavigate();
    const [userType, setUserType] = useState('munim');
    const [payments, setPayments] = useState([]);
    const [payment, setPayment] = useState({
        clientId: params.id,
        date: new Date(),
        amount: Number
    });
    const [reload, setReload] = useState(false);
    useEffect(()=>{
        Authenticated()
        .then((data)=>{
            if(data.isAuth){
                if(data.user.type=="munim"){
                   window.alert("you don't have access to this page");
                   navigate('/home'); 
                }
                else
                setUserType(data.user.type);
            }
            else{navigate('/');window.alert("session expired");}
        });
        getPaymentsById(params.id)
        .then((data)=>{
            data = data.reverse();
            setPayments(data);
        })
    }, [reload]);

    const handleSubmit = (e) =>{
        e.preventDefault();
        console.log(payment);
        addPayment(payment)
        .then(()=>{
            reload?setReload(false):setReload(true);
            window.alert("added successfull");
        })
        .catch((err)=>console.error(err));
    }
    
    return (
        <>
            {userType == "munim"? null: <div className="container-sm">
                {payments.map((payment, i)=>{
                    return <div className="accordion mb-4 shadow-sm" id={`payment_${i}`}>
                    <div className="accordion-item">
                        <h2 className="accordion-header" id="headingOne">
                            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target={`#${payment._id}`} aria-expanded="false" aria-controls={payment._id}>
                                <div className="w-100 d-flex">
                                    <div className="p-2 flex-grow-1 fs-5">{Helper.dateformat(new Date(payment.date))}</div>
                                    <div className="p-2 me-3 fs-6"><b>&#8377;{payment.amount}</b></div>
                                </div>
                            </button>
                        </h2>
                        <div id={payment._id} className="accordion-collapse collapse" aria-labelledby="headingOne" data-bs-parent={`payment_${i}`}>
                            <div className="accordion-body">
                            <button type="button" className="btn btn-secondary p-2" onClick={()=>window.alert("download receipts feature is not ready yet")}>Receipt</button>
                            </div>
                        </div>
                    </div>
                </div>
                })}
            </div>}
            {userType=='munim'?null:<AddBtn/>}

            <div className="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content">
                        <form onSubmit={handleSubmit}>
                            <div className="modal-header">
                                <h1 className="modal-title fs-5" id="staticBackdropLabel">Add payment for this client</h1>
                                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div className="modal-body">
                                <div className="mb-3">
                                    <DatePicker className="form-select mb-3" selected={payment.date} onChange={date => setPayment({ ...payment, date: date })} />
                                </div>
                                <div className="mb-3">
                                    <lable className="me-3">Amount :</lable>
                                    <input type='number' value={payment.amount} className="form-control d-inline w-75" placeholder="enter amount" onChange={e=>setPayment({ ...payment, amount: e.target.value*1 })} />
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                <button type="submit" className="btn btn-primary" data-bs-dismiss="modal">Add payment</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    );
}