import React from 'react';
import { Link } from 'react-router-dom';
import logo from '../android-chrome-192x192.png';

export default function Header(){
    return(
        <div className='mb-5' style={{borderBottom: "3px solid rgba(0,0,0,0.1)", paddingBottom: "10px"}}>
            <nav className="navbar navbar-expand-lg">
                <div className="container-fluid position-relative" style={{height:"70px"}}>
                <Link to='/home' className="fs-1 navbar-brand position-absolute"><img height={150} src={logo} alt='logo'/></Link>
                </div>
            </nav>
        </div>
    );
}