import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { addClient, getClients } from "../api";
import AddBtn from "../components/AddBtn";
import Loading from "../components/Loading";
import Authenticated from "../utils/Auth";

export default function Clients() {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    const [userType, setUserType] = useState('munim');
    const [searchData, setSearchData] = useState("");
    const [clients, setClients] = useState([]);
    const [client, setClient] = useState({
        fullname: "",
        nickname: "",
        address: "",
        state: "",
        phnno: ""
    });
    const [reload, setReload] =useState(false);
    useEffect(()=>{
        Authenticated()
        .then((data)=>{
            if(data.isAuth){
                if(data.user.type=="munim"){
                   window.alert("you don't have access to this page");
                   navigate('/home'); 
                }
                else
                setUserType(data.user.type);
            }
            else{navigate('/');window.alert("session expired");}
        });
        getClients()
        .then((data)=>{
            setClients(data);
            console.log(data);
            setLoading(false);
        });
    }, [reload]);

    const handleSubmit = (e) => {
        e.preventDefault();
        addClient(client)
        .then((data)=>{
            setReload(true);
            window.alert('added successfull');
            console.log(data);
        }).catch((err)=>console.log(err));
    }

    const handleSearch =async (e) => {
        e.preventDefault();
        setLoading(true);
        if(searchData != ""){
        let s = searchData.toLowerCase();
        let temp = await getClients();
        temp = temp.filter((c)=> (c.fullname.toLowerCase().includes(s) || c.nickname.toLowerCase().includes(s)));
        setClients(temp);
        setLoading(false);
        }else{
            setLoading(true);
        getClients()
        .then((data)=>{
            setClients(data);
            console.log(data);
            setLoading(false);
        });
        }
    }
    
    return (
        <div className="container-sm">  
            {loading?<Loading/>:null}
            <form onSubmit={handleSearch} className="d-flex mb-4 justify-content-end" role="search">
                <input className="form-control me-2 search" type="search" value={searchData} onChange={(e)=>setSearchData(e.target.value)} placeholder="Search" aria-label="Search" />
                    <button className="btn btn-outline-success" type="submit">Search</button>
            </form>
            {
                clients.map((client)=>{
                    return <div className="card mb-3 p-2 fs-5 card-body">
                        <div className="w-100 d-flex" data-bs-toggle="collapse" data-bs-target={`#${client._id}`} aria-expanded="false">
                            <div className="p-2 flex-grow-1">{client.fullname}</div>
                            <div className="p-2 text-danger">Due : &#8377; {client.paymentDue}</div>
                        </div>
                        <div className="collapse" id={client._id}>
                                <div className="p-2 mb-3">
                                    <p className="mb-1"><b>Name : </b>{client.fullname}</p>
                                    <p className="mb-1"><b>address : </b>{client.address}</p>
                                    <p className="mb-1"><b>State : </b>{client.state}</p>
                                    <p className="mb-1"><b>Mobile : </b>{client.phnno}</p>
                                </div>
                                <Link to={`/clients/orders/${client._id}`} type="button" className="btn btn-primary me-2">orders</Link>
                                <Link to={`/clients/payments/${client._id}`} type="button" className="btn btn-success">payments</Link>
                        </div>
                    </div>
                })
            }
            {userType=='boss'?<AddBtn/>:null}

            <div className="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content">
                        <form onSubmit={handleSubmit}>
                        <div className="modal-header">
                            <h1 className="modal-title fs-5" id="staticBackdropLabel">Add Client</h1>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                            <div className="modal-body">
                                <div className="mb-3">
                                <lable className="me-3">Name :</lable>
                                <input value={client.fullname} className="form-control d-inline w-75" placeholder="enter name" onChange={(e) => { setClient({ ...client, fullname: e.target.value }) }} />
                                </div>
                                <div className="mb-3">
                                <lable className="me-3">Nickname :</lable>
                                <input value={client.nickname} className="form-control d-inline w-75" placeholder="enter nickname" onChange={(e) => { setClient({ ...client, nickname: e.target.value }) }} />
                                </div>
                                <div className="mb-3">
                                <lable className="me-3">Address :</lable>
                                <input value={client.address} className="form-control d-inline w-75" placeholder="enter address" onChange={(e) => { setClient({ ...client, address: e.target.value }) }} />
                                </div>
                                <div className="mb-3">
                                <lable className="me-3">State :</lable>
                                <input value={client.state} className="form-control d-inline w-75" placeholder="enter state" onChange={(e) => { setClient({ ...client, state: e.target.value }) }} />
                                </div>
                                <div className="mb-3">
                                <lable className="me-3">Phone no. :</lable>
                                <input value={client.phnno} className="form-control d-inline w-75" placeholder="enter phone no." onChange={(e) => { setClient({ ...client, phnno: e.target.value }) }} />
                                </div>
                            </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                            <button type="submit" className="btn btn-primary" data-bs-dismiss="modal">Add client</button>
                        </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
}