import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { down_invoice, getAllOrders, getItems } from "../api";
import Authenticated from "../utils/Auth";
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
import Loading from "../components/Loading";
import { logDOM } from "@testing-library/react";
import { calTotal, Filter } from "../utils/Filter";
import { Helper } from "../utils/Helper";

export default function AllOrders() {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    const [userType, setUserType] = useState('munim');
    const [Orders, setOrders] = useState([]);
    const [FOrders, setFOrders] = useState([]);
    const [items, setItems] = useState([]);
    const [filter, setFilter] = useState({
        fitem: ""
    });
    const [extra, setExtra] = useState({
        total: 0
    });
    const [reload, setReload] = useState(false);
    useEffect(()=>{
        Authenticated()
        .then((data)=>{
            if(data.isAuth){
                if(data.user.type=="munim"){
                   window.alert("you don't have access to this page");
                   navigate('/home'); 
                }
                else
                setUserType(data.user.type);
            }
            else{navigate('/');window.alert("session expired");}
        });
        getAllOrders()
        .then((data)=>{
            setOrders(data);
            calTotal(data)
            .then((d)=>{
                setFOrders(d.orders);
                setExtra({...extra, total: d.total});
            });
        });
        getItems()
        .then(data=>{
            setItems(data);
            setLoading(false);
        });
    }, [reload]);

    const downloadInvoice = (id, clientName, Odate) => {
        setLoading(true);
        let date = Helper.dateformat(new Date(Odate));
        down_invoice(id)
        .then((data)=>{
            const linkSource = `data:application/pdf;base64,${data.pdf}`;
            const downloadLink = document.createElement("a");
            const fileName = `${clientName}-${date}-${id}.pdf`;

            downloadLink.href = linkSource;
            downloadLink.download = fileName;
            downloadLink.click();
            setLoading(false);
        })
        .catch(err=>console.log(err));
    }
    const handleItemChange = (e) => {
        setFilter({...filter, fitem: e.target.value});
    }

    const handleFilter=(e)=>{
        e.preventDefault();
        Filter(Orders, filter)
        .then((d)=>{
            setFOrders(d.orders);
            setExtra({...extra, total: d.total});
        });
    }
    const clearFilter=(e)=>{
        e.preventDefault();
        setFilter({fitem: ""});
        reload?setReload(false):setReload(true);
    }

    return (
        <div className="container-sm">
            <div className="card mb-3 p-2 fs-5 card-body" style={{width: "fit-content"}}>
                <div className="dropdown-toggle" data-bs-toggle="collapse" style={{cursor: 'pointer'}} data-bs-target="#filter" aria-expanded="false"><i className="bi bi-funnel"></i></div>
                <div className="collapse mt-2" id="filter">
                    <div className="p-2 mb-3 d-flex gap-2">
                        <DatePicker className="form-select mb-1" placeholderText="start date" selected={filter.startDate} onChange={date => setFilter({ ...filter, startDate: date })} />
                        <span>to</span>
                        <DatePicker className="form-select mb-1" placeholderText="end date" selected={filter.endDate} onChange={date => setFilter({ ...filter, endDate: date })} />
                    </div>
                    <span className="fs-5">Items : </span><select value={filter.fitem} onChange={handleItemChange} className="form-select me-2" style={{ display: "inline", width: "auto" }}>
                        <option className="text-muted" selected>select item</option>
                        {items.map((item) => {
                            return <option key={item._id} value={item.itemName}>{item.itemName}</option>
                        })}
                    </select>
                    <div className="d-flex mt-3 mb-2 justify-content-end"><button type="button" className="btn btn-secondary me-3 p-2" onClick={clearFilter}>clear</button><button type="button" className="btn btn-success p-2" onClick={handleFilter}>search</button></div>
                </div>
            </div>
            {loading?<Loading/>:null}
            <div className="mb-3 text-end"><b>{filter.fitem} Total : {extra.total}</b></div>
            {FOrders.map((order, i) => {
                return <div className="accordion mb-4 shadow-sm" id={i}>
                    <div className="accordion-item">
                        <h2 className="accordion-header" id="headingOne">
                            <button className="accordion-button collapsed bg-light" type="button" data-bs-toggle="collapse" data-bs-target={`#${order._id}`} aria-expanded="false" aria-controls={order._id}>
                                <div className="w-100 d-flex o-f">
                                    <div className="p-2 flex-grow-1 text-break">{order.clientName}</div>
                                    <div className="p-2">{Helper.dateformat(new Date(order.date))}</div>
                                    <div className="p-2 me-3"><b>&#8377;{ Helper.calculateTotal(order)}</b></div>
                                    <div className={`p-2 ${order.status=="pending"?'text-danger':'text-success'} me-3`}><b>{order.status}</b></div>
                                </div>
                            </button>
                        </h2>
                        <div id={order._id} className="accordion-collapse collapse" aria-labelledby="headingOne" data-bs-parent={`#${i}`}>
                            <div className="accordion-body">
                                <table className="table table-striped">
                                    <thead>
                                        <tr>
                                            <th scope="col">#</th>
                                            <th scope="col">Name</th>
                                            <th scope="col">EQtn</th>
                                            <th scope="col">Qtn</th>
                                            <th scope="col">Rate</th>
                                            <th scope="col">Amount</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {order.items.map((item, index) => {
                                            return <tr>
                                                <th scope="row">{++index}</th>
                                                <td>{item.itemName}</td>
                                                <td>{item.esQuantity}</td>
                                                <td>{item.quantity}</td>
                                                <td>{item.itemRate}</td>
                                                <td>{item.amount}</td>
                                            </tr>
                                        })}
                                    </tbody>
                                </table>
                                <button type="button" className="btn btn-secondary p-2" onClick={()=>downloadInvoice(order._id, order.clientName, order.date)}>Invoice</button>
                            </div>
                        </div>
                    </div>
                </div>
            })}
        </div>
    );
}