import { getUser } from "../api";

export default async function Authenticated(){
    var res = {isAuth: false, user:{}}
    var userid = window.localStorage.getItem('freshagriuser');
    if(userid){
        let user = await getUser({id: userid});
        res = {
            isAuth: true,
            user: user
        };
        return res;
    }
    else return res;
}