export async function calTotal(orders, fitem){
    let total = 0;
    orders.forEach((o)=>{
        o.items.forEach((item)=>{
            if(fitem){
                if(item.itemName == fitem)
                total += item.amount;
            }
            else total += item.amount;
        });
    });
    return { orders: orders, total: total}
}

function filterByItem(fitem, Orders, orders){
    if(fitem){
        let temparr = orders?orders:Orders;
        let temp = [];
        temparr.forEach((o) => {
            const { items } = o;
            for (let i = 0; i < items.length; i++) {
                if (items[i].itemName == fitem) {
                    temp.push(o);
                    break;
                }
            }
        });
        return calTotal(temp, fitem);
    }
    else{
        if(orders) return calTotal(orders, fitem);
        else return;
    }
}

export function Filter(Orders, filter){
    const { startDate, endDate, fitem }  = filter;

    if (startDate) {
        if (endDate) {
            let temp = Orders.filter(o => new Date(o.date) >= startDate && new Date(o.date) <= endDate);
            return filterByItem(fitem, Orders, temp);
        }
        else {
            let temp = Orders.filter(o => new Date(o.date) >= startDate);
            return filterByItem(fitem, Orders, temp);
        }
    }
    else {
        if (endDate) {
            let temp = Orders.filter(o => new Date(o.date) <= endDate);
            return filterByItem(fitem, Orders, temp);
        }
        else return filterByItem(fitem, Orders);
    }
}