import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { loginApi } from "../api";
import Authenticated from "../utils/Auth";

export default function Login() {
    const navigate = useNavigate();

    useEffect(() => {
        Authenticated()
        .then((data) => {
            if (data.isAuth)
            navigate('/home');
        })
    }, []);

    const [user, setUser] = useState({
        userName: "",
        password: ""
    });

    const handleSubmit = (e) => {
        e.preventDefault();
        loginApi(user)
            .then((data) => {
                if (data) {
                    window.localStorage.setItem('freshagriuser', data._id);
                    navigate('/home');
                }
            }).catch((err) => window.alert("invalid username or password"));
    }

    return (
        <div className="container-sm w-50">
            <form onSubmit={handleSubmit}>
                <div className="mb-3">
                    <label htmlFor="exampleInputEmail1" className="form-label">Username</label>
                    <input type='text' className="form-control" value={user.userName} required onChange={(e) => { e.preventDefault(); setUser({ userName: e.target.value, password: user.password }) }} />
                </div>
                <div className="mb-3">
                    <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                    <input type="password" className="form-control" id="exampleInputPassword1" value={user.password} required onChange={(e) => { e.preventDefault(); setUser({ userName: user.userName, password: e.target.value }) }} />
                </div>
                <button type="submit" className="btn btn-primary">Submit</button>
            </form>
        </div>
    );
}