import { CLIENT_ADDED, DONE_ORDER, PAYMENT_ADDED, UPDATE_ORDER } from "../utils/constant";

export async function loginApi(user){
    var data;
    await fetch('/api/login', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(user)
        })
        .then((response) => response.json())
        .then(temp=>data=temp)
        .catch((err)=>window.alert("invalid username or password"));
    return data;
}

export async function getUser(user){
    var data;
    await fetch(`/api/login/${user.id}`)
        .then((response) => response.json())
        .then(temp=>data=temp)
        .catch((err)=>window.alert("not loggedin"));
    return data;
}

