//log
async function Log(title, Id, date){
    await fetch('/api/logs',{
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
            title: title,
            Id: Id,
            date: date
        })
    })
    .catch((err)=>window.alert(err));
}

export async function getLogs(){
    var data;
    await fetch('/api/logs')
    .then((resp)=>resp.json())
    .then((temp)=>data=temp)
    .catch((err)=>window.alert(err));
    return data;
}