import './App.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Login from './pages/Login';
import Home from './pages/Home';
import Header from './components/Header';
import DailyOrders from './pages/DailyOrders';
import Items from './pages/Items';
import Clients from './pages/Clients';
import Footer from './components/Footer';
import ClientOrders from './pages/ClientOrders';
import ClientPayments from './pages/CLientPayments';
import AllOrders from './pages/AllOrders';
import Logs from './pages/Logs';

function App() {
  return (
    <Router>
      <Header/>
      <Routes>
        <Route exact path='/' element={<Login />} />
        <Route exact path='/home' element={<Home />} />
        <Route exact path='/daily-order' element={<DailyOrders />} />
        <Route exact path='/orders' element={<AllOrders />} />
        <Route exact path='/items' element={<Items />} />
        <Route exact path='/clients' element={<Clients />} />
        <Route exact path='/clients/orders/:id' element={<ClientOrders />} />
        <Route exact path='/clients/payments/:id' element={<ClientPayments />} />
        <Route exact path='/logs' element={<Logs/>}/>
      </Routes>
      <div className='w-100' style={{height: "75px"}}></div>
      <Footer/>
    </Router>
  );
}

export default App;
