export class Helper{
    static calculateTotal(o){
        let total=0;
        for(let i=0;i<o.items.length; i++){
            total=total+o.items[i].amount;
        }
        // console.log(total);
        return total;
    }
    static dateformat(d){
        let date = d.getDate()+"-"+(d.getMonth()+1)+"-"+d.getFullYear();
        return date;
    }
}