import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import Authenticated from "../utils/Auth";
import vegi from "../assets/vegetables.jpg";

export default function Home() {
    const navigate = useNavigate();
    const [userType, setUserType] = useState('munim');
    useEffect(()=>{
        Authenticated()
        .then((data)=>{
            if(data.isAuth)
            setUserType(data.user.type);
            else{navigate('/');window.alert("session expired");}
        });
    }, []);
    
    return (
        <div className="d-flex flex-wrap justify-content-center container-sm gap-5">
            <Link to='/daily-order' style={{textDecoration:"none"}}><div className="card homecards border-0 border-bottom homeicon">
                <img src="https://cdn.pixabay.com/photo/2016/09/16/09/20/box-1673579_960_720.png" className="card-img-top" alt="daily orders" />
                <div className="card-body">
                    <p className="card-text fs-5 text-center" style={{fontWeight:"bold", color:"grey"}}>Daily Orders</p>
                </div>
            </div></Link>
            <Link to='/items' style={{textDecoration:"none"}}><div className="card homecards border-0 border-bottom homeicon">
                <img src={vegi} className="card-img-top" alt="items" />
                <div className="card-body">
                    <p className="card-text fs-5 text-center" style={{fontWeight:"bold", color:"grey"}}>Items</p>
                </div>
            </div></Link>
            {userType == 'munim' ? null : <Link to='/clients' style={{textDecoration:"none"}}><div className="card homecards border-0 border-bottom homeicon">
                <img src="https://cdn.pixabay.com/photo/2016/03/31/20/37/client-1295901_960_720.png" className="card-img-top" alt="clients" />
                <div className="card-body">
                    <p className="card-text fs-5 text-center" style={{fontWeight:"bold", color:"grey"}}>Clients</p>
                </div>
            </div></Link>}
            {userType == 'munim' ? null : <Link to='/orders' style={{textDecoration:"none"}}><div className="card homecards border-0 border-bottom homeicon">
                <img src="https://cdn.pixabay.com/photo/2016/09/16/09/20/box-1673579_960_720.png" className="card-img-top" alt="clients" />
                <div className="card-body">
                    <p className="card-text fs-5 text-center" style={{fontWeight:"bold", color:"grey"}}>Orders</p>
                </div>
            </div></Link>}
            {userType == 'boss' ? <Link to='/logs' style={{textDecoration:"none"}}><div className="card homecards border-0 border-bottom homeicon">
                <img src="https://cdn.pixabay.com/photo/2012/04/26/19/44/eye-42922_960_720.png" className="card-img-top" alt="logs" />
                <div className="card-body">
                    <p className="card-text fs-5 text-center" style={{fontWeight:"bold", color:"grey"}}>Logs</p>
                </div>
            </div></Link> : null}
        </div>
    );
}