import React from "react";

export default function AddBtn() {
    return (
        <button type="button" className="addButton bg-success border-0 position-absolute" style={{ width: "75px", height: "55px", bottom: "120px", right: "0", borderRadius: "50px 0 0 50px" }} data-bs-toggle="modal" data-bs-target="#staticBackdrop">
            <i className="bi bi-plus" style={{ fontSize: "37px", color: "white" }}></i>
        </button>
    );
}