import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { addOrder, doneOrder, down_invoice, getClients_wot, getD_orders, getItems, updateOrder } from "../api";
import Authenticated from "../utils/Auth";
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
import Loading from "../components/Loading";
import { Helper } from "../utils/Helper";
import AddBtn from "../components/AddBtn";

export default function Daily_Orders() {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    const [userType, setUserType] = useState('munim');
    const [dOrders, setDOrders] = useState([]);
    const [clients, setclients] = useState([]);
    const [itemAdded, setItemAdded] = useState(false);
    const [items, setItems] = useState([]);
    const [edit, setEdit] = useState({});
    const [item, setItem] = useState({
        itemId: "",
        itemName: "",
        esQuantity: "",
        quantity: "",
        itemRate: "",
        amount: 0
      });
    const [order, setOrder] = useState({
            clientId: "",
            clientName: "",
            date: new Date(),
            items: [],
            status: "pending"
        });
    const [reload, setReload] = useState(false);
    useEffect(()=>{
        Authenticated()
        .then((data)=>{
            if(data.isAuth)
            setUserType(data.user.type);
            else{navigate('/');window.alert("session expired");}
        });
        getD_orders()
        .then((data)=>{
            setDOrders(data);
            console.log(data);
        })
        getClients_wot()
        .then((data)=>{
            setclients(data);
        })
        .catch((err)=>console.log(err));
        getItems()
        .then(data=>{
            setItems(data);
            setLoading(false);
        })
    }, [reload]);

    const handleSubmit = (e) => {
        e.preventDefault();
        console.log(order);
        addOrder(order)
        .then((data)=>{
            if(reload===true)setReload(false);else setReload(true);
            console.log('added successfull');
            console.log(data);
        }).catch((err)=>console.log(err));
    }
    const handleClientChange = (e) => {
        let val = clients.find(clt=>clt._id===e.target.value);
        setOrder({...order, clientId: val._id, clientName: val.fullname});
    }
    const handleItemSubmit = (e) => {
        e.preventDefault();
        order.items.push(item);
        setItem({
            itemId: "",
            itemName: "",
            esQuantity: "",
            quantity: "",
            itemRate: "",
            amount: 0
        });
        setItemAdded(true);
    }
    const handleItemChange = (e) => {
        let val = items.find(ite=>ite._id===e.target.value);
        setItem({...item, itemId: val._id, itemName: val.itemName});
    }
    const onChangeInput = (e, i, oId, index) => {
        // debugger;
        // edit[oId] ={...edit[oId], [index]: e.target.value}
        // console.log(edit);
        dOrders[i].items[index].quantity = e.target.value;
    }
    const onSent = (id) => {
        for(let i=0; i<dOrders.length; i++){
            if(id == dOrders[i]._id){
            updateOrder(id, dOrders[i].items)
            .then(()=>reload?setReload(false):setReload(true));
            }
        }
    }
    const onDone = (id) => {
        for(let i=0; i<dOrders.length; i++){
            if(id == dOrders[i]._id){
            doneOrder(id)
            .then(()=>reload?setReload(false):setReload(true));
            }
        }
    }
    const downloadInvoice = (id, clientName, Odate) => {
        setLoading(true);
        let date = Helper.dateformat(new Date(Odate));
        down_invoice(id)
        .then((data)=>{
            const linkSource = `data:application/pdf;base64,${data.pdf}`;
            const downloadLink = document.createElement("a");
            const fileName = `${clientName}-${date}-${id}.pdf`;

            downloadLink.href = linkSource;
            downloadLink.download = fileName;
            downloadLink.click();
            // console.log(data.pdf);
            setLoading(false);
        })
        .catch(err=>console.log(err));
    }
    return (
        <div className="container-sm">
            {loading?<Loading/>:null}
            {dOrders.length?dOrders.map((order, i) => {
                return <div className="accordion mb-4 shadow-sm" id={i}>
                    <div className="accordion-item">
                        <h2 className="accordion-header" id="headingOne">
                            <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target={`#${order._id}`} aria-expanded="false" aria-controls={order._id}>
                                <div className="w-100 d-flex">
                                    <div className="p-2 flex-grow-1 fs-5">{order.clientName}</div>
                                    <div className={`p-2 ${order.status=="pending"?'text-danger':'text-warning'} me-3 fs-5`}><b>{order.status}</b></div>
                                </div>
                            </button>
                        </h2>
                        <div id={order._id} className="accordion-collapse collapse" aria-labelledby="headingOne" data-bs-parent={`#${i}`}>
                            <div className="accordion-body">
                                <table className="table table-striped">
                                    <thead>
                                        <tr>
                                            <th scope="col">#</th>
                                            <th scope="col">Name</th>
                                            <th scope="col">EQtn</th>
                                            <th scope="col">Qtn</th>
                                            <th scope="col">Rate</th>
                                            <th scope="col">Amount</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {order.items.map((item, index) => {
                                            return <tr>
                                                <th scope="row">{++index}</th>
                                                <td>{item.itemName}</td>
                                                <td>{item.esQuantity}</td>
                                                <td>{order.status=="sent"?<p>{item.quantity}</p>:<input className="realqt"
                                                    value={edit[i]} 
                                                    type='number'
                                                    onChange={(e)=>onChangeInput(e, i, order._id, index-1)}/>}
                                                </td>
                                                <td>{item.itemRate}</td>
                                                <td>{item.amount}</td>
                                            </tr>
                                        })}
                                    </tbody>
                                </table>
                                {order.status=="pending"?<button type="button" className="btn btn-warning" onClick={()=>onSent(order._id)}>Sent</button>:userType=="munim"?null:<div><button type="button" className="btn btn-success me-3" onClick={()=>onDone(order._id)}>Done</button><button type="button" className="btn btn-secondary p-2" onClick={()=>downloadInvoice(order._id, order.clientName, order.date)}>Invoice</button></div>}
                            </div>
                        </div>
                    </div>
                </div>
            }):
            <p className="text-center text-secondary">No Orders</p>
        }



            {/* addorder */}

            {userType=='boss'?<AddBtn/>:null}

            <div className="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered">
                    <div className="modal-content">
                        <form onSubmit={handleSubmit}>
                        <div className="modal-header">
                            <h1 className="modal-title fs-5" id="staticBackdropLabel">Add Order</h1>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div className="modal-body">
                                <label>Date:</label>
                                <DatePicker className="form-select mb-3" selected={order.date} onChange={date => setOrder({ ...order, date: date })} />
                                <select value={order.clientId} onChange={handleClientChange} className="form-select mb-3" aria-label="Default select example">
                                <option selected>select client name</option>
                                    {clients.map((client) => {
                                        return <option key={client._id} value={client._id}>{client.fullname}</option>
                                    })}
                                </select>
                                <table className="table table-striped">
                                    <thead>
                                        <tr>
                                            <th scope="col">#</th>
                                            <th scope="col">Name</th>
                                            <th scope="col">EQtn</th>
                                            <th scope="col">Rate</th>
                                            <th scope="col">Amount</th>
                                        </tr>
                                    </thead>
                                    {itemAdded ? <tbody>{order.items.map((item, index) => {
                                        return <tr>
                                            <th scope="row">{++index}</th>
                                            <td>{item.itemName}</td>
                                            <td>{item.esQuantity}</td>
                                            <td>{item.itemRate}</td>
                                            <td>{item.amount}</td>
                                            <td onClick={()=>{
                                                let itm = order.items;
                                                itm.splice(index-1,1);
                                                setOrder({...order, items: itm});
                                            }}><div className="text-danger" style={{transform:'scale(3)', cursor:'pointer'}} >-</div></td>
                                        </tr>
                                    })}</tbody> : <tr className="w-100" style={{height:"50px"}}><span className="position-absolute top-50 start-50 translate-middle text-muted">no items</span></tr>}
                                </table>
                                <div className="border-top pt-3">
                                    <h3 className="fs-6">Add items</h3>
                                    <div className="my-3">
                                        <select value={item.itemId} onChange={handleItemChange} className="form-select me-2" style={{ display: "inline", width: "auto" }}>
                                            <option selected>select item</option>
                                            {items.map((item) => {
                                                return <option key={item._id} value={item._id}>{item.itemName}</option>
                                            })}
                                        </select>
                                        <input type='number' class="form-control me-2" style={{ display: "inline", maxWidth: "20%" }} placeholder="Qtn" value={item.esQuantity} onChange={(e) => setItem({ ...item, esQuantity: e.target.value, amount: item.itemRate * e.target.value })} />
                                        <input type='number' class="form-control me-3" style={{ display: "inline", maxWidth: "20%" }} placeholder="Rate" value={item.itemRate} onChange={(e) => setItem({ ...item, itemRate: e.target.value, amount: item.esQuantity * e.target.value })} />
                                        <b className="fs-5">&#8377;{item.amount}</b>
                                    </div>
                                    
                                    <div className="text-center"><button className="btn btn-primary" onClick={handleItemSubmit}>Add</button></div>
                                </div>
                                {/* <button type='submit' >Place Order</button> */}
                            
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                            <button type="submit" className="btn btn-primary" data-bs-dismiss="modal">Place Order</button>
                        </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
}